import { Component, Injector, OnInit } from '@angular/core';
import { Validators } from '@angular/forms';
import { BaseResourceFormComponent } from 'app/shared/containers/base-resource-form';
import { IFilter } from 'app/shared/models/interface.model';
import { PermissaoService } from '../../api/permissao.service';
import { Permissao, PermissaoResponse } from '../../permissao.mode';
import toastr from 'toastr';

@Component({
  selector: 'app-permissao-form',
  templateUrl: './permissao-form.component.html',
  styleUrls: ['./permissao-form.component.scss'],
})
export class PermissaoFormComponent
  extends BaseResourceFormComponent<Permissao, PermissaoResponse>
  implements OnInit
{
  permissoes: Permissao[] = [];

  constructor(
    protected permissaoService: PermissaoService,
    protected injector: Injector
  ) {
    super(injector, new Permissao(), permissaoService);
  }

  ngOnInit(): void {
    super.ngOnInit();
    this.carregaPermissoes();
  }

  protected buildResourceForm() {
    this.resourceForm = this.formBuilder.group({
      descricao: [null, [Validators.required, Validators.minLength(2)]],
      permissaoPai: [],
    });
  }

  protected carregaPermissoes() {
    const filtro: IFilter = {
      filtro: '',
      pagina: 0,
      direcaoOrdenacao: 'DESC',
      tamanho: 100,
      ordenarPor: 'id',
    };

    this.permissaoService.getAll(filtro).subscribe(
      (permissoes) => {
        this.permissoes = permissoes.content;
        this.resourceForm.patchValue({
          permissaoPai: this.resource?.permissaoPai?.id,
        });
      },
      (err) =>
        toastr.error(
          'Não foi possível carregar as permissões',
          err.error.error,
          {
            timeOut: 10000,
            progressBar: true,
            closeButton: true,
          }
        )
    );
  }

  protected createPageTitle(): string {
    return 'Cadastro de Novo Permissao';
  }

  protected editionPageTitle(): string {
    const permissaoNome = this.resource.descricao || '';
    return 'Editando permissão: ' + permissaoNome;
  }
}
