import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { Route, RouterModule } from '@angular/router';
import { PermissaoFormComponent } from './containers/permissao-form/permissao-form.component';
import { PermissaoListComponent } from './containers/permissao-list/permissao-list.component';
import { SharedModule } from 'app/shared/shared.module';

const routes: Route[] = [
  { path: '', component: PermissaoListComponent },
  { path: 'novo', component: PermissaoFormComponent },
  { path: ':id/editar', component: PermissaoFormComponent },
];

@NgModule({
  declarations: [PermissaoFormComponent, PermissaoListComponent],
  imports: [CommonModule, RouterModule.forChild(routes), SharedModule],
})
export class PermissaoModule {}
